import {
	Component,
	ViewChild,
	ElementRef,
	AfterViewInit
} from '@angular/core'

import { Pages } from '../enums/pages.enum'
import Chrome from '../services/chrome'

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css']
})
export default class AppComponent {

	page: Pages

	constructor(private chrome: Chrome) {

		chrome.get('token', token => {
			console.log(token)
		})

		console.log()

	}

}



