import { Injectable } from '@angular/core'

enum ChromeMethod {
	Get = 'get',
	Set = 'set',
	Inject = 'inject'
}

class ChromeRequest {
	method: ChromeMethod
	key: string
	value: string
}

@Injectable()
export default class Chrome {

	private chromeRequest = (request: ChromeRequest, callback): void => {
		window['chrome'].extension.sendRequest(request, result => callback(result))
	}

	get = (key: string, callback): void => {
		this.chromeRequest({
			method: ChromeMethod.Get,
			key: key,
			value: null
		}, result => callback(result))
	}

	set = (key: string, value: string): void => {
		this.chromeRequest({
			method: ChromeMethod.Set,
			key: key,
			value: value
		}, r => null)
	}

	inject = (key: string): void => {
		this.chromeRequest({
			method: ChromeMethod.Inject,
			key: key,
			value: null
		}, r => null)
	}

}



